import React from 'react'
import { AiOutlineMenu, AiOutlineGlobal } from 'react-icons/ai'
import { VscChromeClose } from 'react-icons/vsc'
import AppStoreButton from '../assets/appStoreButton.webp'
import PlayStoreButton from '../assets/playStoreButton.webp'

import LogoWhite from '../assets/cryptoWhite.webp'
import LogoBlue from '../assets/cryptoBlue.webp'
import useToggle from './toggle'
import NavItem from './NavItem'

const Nav = () => {
    const [isOn, toggleIsOn] = useToggle()

    let categoryModalStyle = {
        width: isOn ? '100%' : '0',
        visibility: isOn ? 'visible' : 'hidden',
        transform: isOn
            ? 'translateX(0%) translateZ(0px)'
            : 'translateX(600px)',
        transition: 'all .1s',
    }

    let hamburgerStyle = isOn ? (
        <div className='flex z-30 relative'>
            <img src={PlayStoreButton} width='44' height='44' alt='' />
            <img
                src={AppStoreButton}
                width='44'
                height='44'
                alt=''
                className='mx-4'
            />

            <VscChromeClose
                className='text-black self-center text-2xl relative cursor-pointer'
                onClick={toggleIsOn}
            />
        </div>
    ) : (
        <div className='flex z-30 relative'>
            <img src={PlayStoreButton} width='44' height='44' alt='' />
            <img
                src={AppStoreButton}
                width='44'
                height='44'
                alt=''
                className='mx-4'
            />

            <AiOutlineMenu
                className='text-white self-center text-2xl relative cursor-pointer'
                onClick={toggleIsOn}
            />
        </div>
    )

    // transform: translateX(0%) translateZ(0px);

    const categoryModal = (
        <div
            className='fixed top-0 left-0 w-full h-full bg-gray-100 text-black z-20 pt-28'
            style={categoryModalStyle}
        >
            <ul className='categoryModal'>
                <NavItem name='Products' link_path='/' />
                <NavItem name='Company' link_path='/' />
                <NavItem name='Prices' link_path='/' />
                <NavItem name='NFT' link_path='/' span='new' />

                <div className=' mt-6'>
                    <NavItem name='EN' link_path='/' />

                    <li className='py-3 px-6 text-black  lg:self-center font-bold text-lg'>
                        <a href='/'>
                            <AiOutlineGlobal />
                        </a>
                    </li>
                </div>
            </ul>
        </div>
    )
    return (
        <nav className='grid grid-cols-nav justify-between align relative mt-8 sm:mt-4 '>
            <div className='w-full items-center justify-between grid gap-y-4 lg:flex '>
                <img
                    src={isOn ? LogoBlue : LogoWhite}
                    alt=''
                    className='max-w-none z-40'
                    width='136'
                />
            </div>

            <div className='modal lg:hidden justify-self-end'>
                {hamburgerStyle}

                {categoryModal}
            </div>

            <ul className='hidden lg:flex align-middle justify-self-end'>
                <div className='mr-5'>
                <NavItem name='NFT' link_path='/' span='new'/>

                </div>

                <NavItem name='Products' link_path='/' />
                <NavItem name='Company' link_path='/' />
                <NavItem name='Prices' link_path='/' />

                <NavItem name='EN' link_path='/' />

                <li className='py-3 px-6 text-[#aaaaaa] lg:self-center font-bold text-lg'>
                    <a href='/'>
                        <AiOutlineGlobal />
                    </a>
                </li>
            <div className='flex self-center'>
                <img src={PlayStoreButton} width='44' height='44' alt='' />
                <img
                    src={AppStoreButton}
                    width='44'
                    height='44'
                    alt=''
                    className='mx-4'
                />

               
            </div>
            </ul>

        </nav>
    )
}

export default Nav
